@import url('https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  font-family: "Kanit", sans-serif !important;
  line-height: 1.3;
  color: #ebebeb;
  font-weight: normal;
}

input:focus {
  outline: none !important;
}

input::-ms-input-placeholder {
  /* Edge 12-18 */
  color: #556172 !important;
}

input::placeholder,
textarea::placeholder {
  color: #556172 !important;
}

.ant-input-outlined:focus,
.ant-input-outlined:focus-within {
  border: 0 !important;
  box-shadow: none !important;
  /* background: #262b3a !important; */
}
.ant-select-selector :focus,
.ant-select-selector :focus-within {
  border: 0 !important;
  box-shadow: none !important;
  /* background: #262b3a !important; */
}

.ant-input-outlined,
.ant-select-selector {
  outline: none !important;
  box-shadow: none !important;
  border: 0 !important;
  border-radius: 10px !important;
}

a {
  cursor: pointer;
  transition: none;
}

img {
  width: 100%;
}

body {
  margin: 0 !important;
  color: #35373F;
  background: #050518;
}

.bg-primary-gradient {
  background: linear-gradient(270deg, #00a2e8, #53e3df);
}
.bg-secondary-gradient {
  background: linear-gradient(270deg, #ff614b, #fd5065);
}

/* Trending */

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

.ant-form-item-margin-offset {
  margin-bottom: 0 !important;
}

.slide {
  height: 100%;
  transition: 300ms all linear;
}

.connect-btn-condition .wallet-adapter-dropdown {
  width: 100%;
}

.ant-layout {
  background: #000000;
  background-image: url(../src/assets/img/background.png);
  background-attachment: fixed;
  background-size: cover;
  background-position: top center;
}
.ant-layout-content {
  max-width: 400px;
  width: 100%;
  margin: auto;
  padding-bottom: 100px;
  padding-right: 80px;
  padding-top: 80px;
  padding-left: 15px;
}


.ant-layout-header {
  height: 80px;
  display: flex;
  align-items: center;
  background-color: #000000;
  background-repeat: no-repeat !important;
  background-size: cover  !important;
}

.ant-menu-inline-collapsed-tooltip {
  display: none;
}

.ant-btn {
  padding: 0;
  margin-left: -20px;
}

.ant-menu.menu-main .ant-menu-item:first-child.ant-menu-item-selected {
  background: none !important;
}

.ant-menu.menu-main .ant-menu-item {
  display: flex;
  align-items: center;
  margin-block: 0;
  padding: 0 !important;
}

.ant-menu-inline.menu-main .ant-menu-item,
.ant-menu-vertical.menu-main .ant-menu-item,
.ant-menu-inline.menu-main .ant-menu-submenu-title,
.ant-menu-vertical.menu-main .ant-menu-submenu-title {
  margin-inline: 0;
  width: 100%;
  border-radius: 0;
  color: #ffffff69;
}

.ant-menu-inline.menu-main .ant-menu-item path{
  fill: #ffffff69;
}

.ant-menu-inline.menu-main .ant-menu-item span{
  margin-inline-start: 0;
  color: #ffffff69;
}

.ant-menu-dark.menu-main .ant-menu-item-selected ,
.ant-menu-dark .menu-main > .ant-menu-item-selected ,
.ant-menu-dark.menu-main .ant-menu-item:hover  {
  background: #181818;
  color: #fff !important;
}
.ant-menu-dark.menu-main .ant-menu-item-selected span,
.ant-menu-dark .menu-main > .ant-menu-item-selected span,
.ant-menu-dark.menu-main .ant-menu-item:hover span {
  color: #fff !important;
}

.ant-menu-dark.menu-main .ant-menu-item-selected path,
.ant-menu-dark.menu-main .ant-menu-item:hover path{
  fill: #fff !important;
}


a:hover {
  color: inherit;
}


/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.ant-picker.ant-picker-outlined {
  border: 0 !important;
}

