.game-slots{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  position: fixed;
  left: 10px;
  right: 10px;
  bottom: 10px;
  max-width: 400px;
  margin: auto;
}
.game-slots .item{
  flex: 1;
}
.game-slots .item .inner{
  position: relative;
  padding-top: 108.5227272727273%;
}

.game-slots .item .inner .link{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-image: url(../src/assets/img/slot_unlock.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
}

.game-slots .item:nth-child(2) .inner .link{
  background-image: url(../src/assets/img/slot2_unlock.png);
}
.game-slots .item:nth-child(3) .inner .link{
  background-image: url(../src/assets/img/slot3_unlock.png);
}
.game-slots .item:nth-child(4) .inner .link{
  background-image: url(../src/assets/img/slot4_unlock.png);
}

.game-slots:after, .game-slots::before{
  content: "";
  width: 52px;
  height: 60px;
  background-image: url(../src/assets/img/left_border.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom;
  position: absolute;
  bottom: -10px;
  z-index: 1;
}
.game-slots:after{
  right: -10px;
  background-image: url(../src/assets/img/right_border.png);
}
.game-slots:before{
  left: -10px;
  
}

